body {
  color: #2d2a26;
  letter-spacing: -.01em;
  background: #fcfcf0;
  font-family: Montserrat, sans-serif;
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: 150%;
}

main {
  flex-direction: column;
  display: flex;
}

.content {
  flex-grow: 1;
}

.container {
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 15px;
}

.button {
  color: #2d2a26;
  border: 1px solid #2d2a26;
  border-radius: 10px;
  font-family: Montserrat, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: 1.35;
}

.button:hover, .button:focus {
  cursor: pointer;
  transform: translate(2px, 2px);
  box-shadow: 3px 3px #2d2a26;
}

.header-button {
  width: 140px;
  height: 54px;
  background: #fcfcf0;
  justify-content: center;
  align-items: center;
  display: flex;
  box-shadow: 5px 5px #2d2a26;
}

.header-button img {
  margin-left: 10px;
}

.user-button {
  display: none;
}

.hero-button {
  width: 159px;
  height: 53px;
  background: #8abadd;
  justify-content: center;
  align-items: center;
